import cloneDeep from 'lodash/cloneDeep';
import { arrayFind } from '@utils/util';

const COLUMNS = 3;
const ROWS = 3;
const defaultConfig = {
  grid: (() => {
    const arr: Array<any> = [];
    for (let i = 1; i <= ROWS; i++) {
      for (let j = 1; j <= COLUMNS; j++) {
        arr.push({
          id: arr.length + 1,
          width: 1,
          height: 1,
          gridColumnStart: j,
          gridColumnEnd: j + 1,
          gridRowStart: i,
          gridRowEnd: i + 1,
          canDrop: 0,
          empty: true
        });
      }
    }
    return arr;
  })()
};

// 
function switchActivedBlock(this: any, block: any) {
  const { grid } = this;
  this.block = arrayFind(grid, 'id', block.id);
  this.leftNeighbors = getHorizontalNeighbors.call(this, -1);
  this.rightNeighbors = getHorizontalNeighbors.call(this, 1);
  this.topNeighbors = getVerticalNeighbors.call(this, -1);
  this.bottomNeighbors = getVerticalNeighbors.call(this, 1);
}

// 
function computeExtendStatus(this: any) {
  const {
    block,
    leftNeighbors,
    rightNeighbors,
    topNeighbors,
    bottomNeighbors
  } = this;
  let canLeft = true;
  let canRight = true;
  let canUp = true;
  let canDown = true;
  let canBackRight = true;
  let canBackLeft = true;
  let canBackUp = true;
  let canBackDown = true;
  // 
  if (block.empty) {
    canLeft = false;
    canRight = false;
    canUp = false;
    canDown = false;
    canBackRight = false;
    canBackLeft = false;
    canBackUp = false;
    canBackDown = false;
  } else {
    // 
    if (block.width === 1) {
      // 
      canBackRight = false;
      canBackLeft = false;
    }
    if (block.height === 1) {
      // 
      canBackUp = false;
      canBackDown = false;
    }

    //  TODO: 
    if (leftNeighbors === 'Unavailable' || !neighborsAllEmpty(leftNeighbors)) {
      // 
      canLeft = false;
    }
    if (
      rightNeighbors === 'Unavailable' ||
      !neighborsAllEmpty(rightNeighbors)
    ) {
      // 
      canRight = false;
    }
    if (topNeighbors === 'Unavailable' || !neighborsAllEmpty(topNeighbors)) {
      // 
      canUp = false;
    }
    if (
      bottomNeighbors === 'Unavailable' ||
      !neighborsAllEmpty(bottomNeighbors)
    ) {
      // 
      canDown = false;
    }
  }

  block.canLeft = canLeft;
  block.canRight = canRight;
  block.canUp = canUp;
  block.canDown = canDown;

  block.canBackRight = canBackRight;
  block.canBackLeft = canBackLeft;
  block.canBackUp = canBackUp;
  block.canBackDown = canBackDown;
}

//  Unavailable
// 
//   1. item.gridColumnStart === neighborsColumnStartdirection1gridColumnStart
//   2. item.width === 1 && item.height === 11
//   3. item.gridRowStart >= block.gridRowStart && item.gridRowEnd <= block.gridRowEnd 
function getHorizontalNeighbors(this: any, direction: any) {
  const { grid, block } = this;
  const horizontalNeighbors: Array<any> = [];
  const neighborsColumnStart =
    direction === 1 ? block.gridColumnEnd : block.gridColumnStart - 1;
  grid.forEach((item: any) => {
    if (
      item.width === 1 &&
      item.height === 1 &&
      item.gridColumnStart === neighborsColumnStart &&
      item.gridRowStart >= block.gridRowStart &&
      item.gridRowEnd <= block.gridRowEnd
    ) {
      horizontalNeighbors.push(item);
    }
  });
  return horizontalNeighbors.length !== block.height
    ? 'Unavailable'
    : horizontalNeighbors;
}

//  Unavailable
function getVerticalNeighbors(this: any, direction: any) {
  const { grid, block } = this;
  const verticalNeighbors: Array<any> = [];
  const neighborsRowStart =
    direction === 1 ? block.gridRowEnd : block.gridRowStart - 1;
  grid.forEach((item: any) => {
    if (
      item.width === 1 &&
      item.height === 1 &&
      item.gridRowStart === neighborsRowStart &&
      item.gridColumnStart >= block.gridColumnStart &&
      item.gridColumnEnd <= block.gridColumnEnd
    ) {
      verticalNeighbors.push(item);
    }
  });
  return verticalNeighbors.length !== block.width
    ? 'Unavailable'
    : verticalNeighbors;
}

// 
function neighborsAllEmpty(neighbors: any) {
  return neighbors.every((item: any) => item.empty);
}

// 
function rowSpan(this: any, direction: any) {
  const { grid, block, leftNeighbors, rightNeighbors } = this;
  block.width++;
  if (direction === 1) {
    // 
    block.gridColumnEnd++;
    rightNeighbors.forEach((item: any) =>
      grid.splice(
        grid.findIndex((innerItem: any) => innerItem.id === item.id),
        1
      )
    );
  } else {
    // 
    block.gridColumnStart--;
    leftNeighbors.forEach((item: any) =>
      grid.splice(
        grid.findIndex((innerItem: any) => innerItem.id === item.id),
        1
      )
    );
  }
}

// 
function rowShorten(this: any, direction: any) {
  const { grid, block } = this;
  let newColumnStart: any = null;
  block.width--;
  if (direction === 1) {
    // 
    newColumnStart = block.gridColumnStart;
    block.gridColumnStart++;
  } else {
    // 
    block.gridColumnEnd--;
    newColumnStart = block.gridColumnEnd;
  }

  for (let i = block.gridRowStart; i < block.gridRowEnd; i++) {
    grid.push({
      id: `${new Date().getTime()}${Math.floor(Math.random() * 900) + 100}`,
      width: 1,
      height: 1,
      gridColumnStart: newColumnStart,
      gridColumnEnd: newColumnStart + 1,
      gridRowStart: i,
      gridRowEnd: i + 1,
      canDrop: 0,
      empty: true
    });
  }
}

// 
function colSpan(this: any, direction: any) {
  const { grid, block, topNeighbors, bottomNeighbors } = this;
  block.height++;
  if (direction === 1) {
    block.gridRowEnd++;
    bottomNeighbors.forEach((item: any) =>
      grid.splice(
        grid.findIndex((innerItem: any) => innerItem.id === item.id),
        1
      )
    );
  } else {
    block.gridRowStart--;
    topNeighbors.forEach((item: any) =>
      grid.splice(
        grid.findIndex((innerItem: any) => innerItem.id === item.id),
        1
      )
    );
  }
}

// 
function colShorten(this: any, direction: any) {
  const { grid, block } = this;
  let newRowStart: any = null;
  block.height--;
  if (direction === 1) {
    // 
    newRowStart = block.gridRowStart;
    block.gridRowStart++;
  } else {
    // 
    block.gridRowEnd--;
    newRowStart = block.gridRowEnd;
  }

  for (let i = block.gridColumnStart; i < block.gridColumnEnd; i++) {
    grid.push({
      id: `${new Date().getTime()}${Math.floor(Math.random() * 900) + 100}`,
      width: 1,
      height: 1,
      gridColumnStart: i,
      gridColumnEnd: i + 1,
      gridRowStart: newRowStart,
      gridRowEnd: newRowStart + 1,
      canDrop: 0,
      empty: true
    });
  }
}

// 
function divination(this: any, block: any) {
  const { draggingBlock: ori } = this;
  let verification = false;

  // 
  if (block.empty) {
    const ow = ori.width;
    const oh = ori.height;

    // tc
    const tc = new CanvasContainer(this.getGrid());
    switchActivedBlock.call(tc, block);
    // tc
    tc.block.empty = false;
    computeExtendStatus.call(tc);
    if (
      canDrop.call(this, ow, tc, 'horizontal') &&
      canDrop.call(this, oh, tc, 'vertical')
    ) {
      verification = true;
    }
  }
  return verification ? 1 : 2;
}

// 
function canDrop(this: any, size: any, tc: any, direction: any): boolean {
  const { block } = tc;
  let canDropFlag = true;
  const putSize = direction === 'horizontal' ? block.width : block.height;
  if (size > putSize) {
    // 
    if (direction === 'horizontal' ? block.canRight : block.canDown) {
      direction === 'horizontal'
        ? tc.blockWidthExtend(block, 1)
        : tc.blockHeightExtend(block, 1);
      switchActivedBlock.call(tc, block);
      // 
      computeExtendStatus.call(tc);
      canDropFlag = canDrop.call(this, size, tc, direction);
    } else {
      canDropFlag = false;
    }
  } else {
    canDropFlag = true;
  }
  return canDropFlag;
}

function handleMoving(this: any, block: any) {
  const { draggingBlock: ori, grid } = this;

  const ow = ori.width;
  const oh = ori.height;
  const goods = ori.goods;
  const oId = ori.id;

  // 
  const newItems: Array<any> = [];
  for (let i = ori.gridRowStart; i < ori.gridRowEnd; i++) {
    for (let j = ori.gridColumnStart; j < ori.gridColumnEnd; j++) {
      newItems.push({
        id: `${new Date().getTime()}${Math.floor(Math.random() * 900) + 100}`,
        width: 1,
        height: 1,
        gridColumnStart: j,
        gridColumnEnd: j + 1,
        gridRowStart: i,
        gridRowEnd: i + 1,
        canDrop: 0,
        empty: true
      });
    }
  }
  grid.splice(
    grid.findIndex((item: any) => item.id === ori.id),
    1,
    ...newItems
  );

  // 
  switchActivedBlock.call(this, block);
  this.block.empty = false;
  this.block.goods = goods;
  for (let i = 1; i < ow; i++) {
    this.blockWidthExtend(block, 1);
    switchActivedBlock.call(this, block);
    computeExtendStatus.call(this);
  }
  for (let i = 1; i < oh; i++) {
    this.blockHeightExtend(block, 1);
    switchActivedBlock.call(this, block);
    computeExtendStatus.call(this);
  }
  this.block.id = oId; // ididswitchActivedBlockid
}

export class CanvasContainer {
  grid: Array<any>;
  block: any;
  leftNeighbors: any;
  rightNeighbors: any;
  topNeighbors: any;
  bottomNeighbors: any;
  draggingBlock: any;
  constructor(grid = cloneDeep(defaultConfig.grid)) {
    this.grid = grid;
  }

  // 
  blockWidthExtend(block: any, direction: any) {
    switchActivedBlock.call(this, block);
    rowSpan.call(this, direction);
  }

  // 
  blockHeightExtend(block: any, direction: any) {
    switchActivedBlock.call(this, block);
    colSpan.call(this, direction);
  }

  // 
  blockWidthShorten(block: any, direction: any) {
    switchActivedBlock.call(this, block);
    rowShorten.call(this, direction);
  }

  // 
  blockHeightShorten(block: any, direction: any) {
    switchActivedBlock.call(this, block);
    colShorten.call(this, direction);
  }

  // gridConfig
  getGrid() {
    this.getAllExtendStatus();
    return cloneDeep(this.grid);
  }

  // 
  getAllExtendStatus() {
    this.grid.forEach((blockItem) => {
      switchActivedBlock.call(this, blockItem);
      computeExtendStatus.call(this);
    });
  }

  // 
  emptyBlock(block: any) {
    switchActivedBlock.call(this, block);
    const {
      gridRowStart,
      gridColumnStart,
      gridColumnEnd,
      gridRowEnd
    } = this.block;
    for (let i = gridRowStart; i < gridRowEnd; i++) {
      for (let j = gridColumnStart; j < gridColumnEnd; j++) {
        if (i === gridRowStart && j === gridColumnStart) {
          continue;
        }
        this.grid.push({
          id: `${new Date().getTime()}${Math.floor(Math.random() * 900) + 100}`,
          width: 1,
          height: 1,
          gridColumnStart: j,
          gridColumnEnd: j + 1,
          gridRowStart: i,
          gridRowEnd: i + 1,
          canDrop: 0,
          empty: true
        });
      }
    }

    this.block.empty = true;
    this.block.goods = null;
    this.block.width = 1;
    this.block.height = 1;
    this.block.gridColumnEnd = this.block.gridColumnStart + 1;
    this.block.gridRowEnd = this.block.gridRowStart + 1;
  }

  // 
  addRow() {
    const { grid } = this;
    const newRowStart = grid.reduce((item1, item2) =>
      item1.gridRowEnd > item2.gridRowEnd ? item1 : item2
    ).gridRowEnd; // gridRowEndgridRowStart
    for (let i = 1; i <= COLUMNS; i++) {
      grid.push({
        id: `${new Date().getTime()}${Math.floor(Math.random() * 900) + 101}`,
        width: 1,
        height: 1,
        gridColumnStart: i,
        gridColumnEnd: i + 1,
        gridRowStart: newRowStart,
        gridRowEnd: newRowStart + 1,
        canDrop: 0,
        empty: true
      });
    }
  }

  // 
  setDraggingBlock(block: any) {
    this.draggingBlock = block;
  }
  // icon
  setDraggingBlockContent(block: any) {
    switchActivedBlock.call(this, block);
    this.block.empty = false;
    this.block.goods = block.goods;
  }
  // 
  checkDragAllow(block: any) {
    if (block !== this.draggingBlock) {
      return divination.call(this, block);
    }
  }
  // icon
  checkIconDrag(block: any) {
    return block.empty ? 1 : 2;
  }
  // 
  moving(block: any) {
    handleMoving.call(this, block);
  }
}
